@import url('https://fonts.googleapis.com/css?family=Raleway');

.modal, .modal button{
    font-family: 'Raleway', serif;
}

.modal *{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.color{
    background: -webkit-gradient(linear, left top, right top, from(#e1e0ff), to(#d7fadd));
    background: -webkit-linear-gradient(left, #e1e0ff, #d7fadd);
    background: -o-linear-gradient(left, #e1e0ff, #d7fadd);
    background: linear-gradient(to right, #e1e0ff, #d7fadd);
}

.modal {
    background: #191919;
    background: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    letter-spacing: 3px;
    color: #b9b9b9;
    display: none;
}

.modal.active {
    display: block;
}

.modal h1{
    padding-bottom: 40px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 16px;
}

.modal p{
    padding-bottom: 15px;
}

.modal p:last-of-type{
    padding-bottom: 0;
}

.try-again{
    cursor: pointer;
    position: relative;
    font-size: 16px;
}

.try-again:after{
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    height: 1px;
    top: 100%;
    background: #ebebeb;
    margin-top: 8px;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.try-again:hover:after{
    left: 40%;
    right: 40%;
}

.modal .wrapper {
    width: 0;
    height: 0;
}

.modal.active .wrapper{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.button{
    cursor: pointer;
    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    background: none;
    outline: none;
    border: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    border-radius: 2px;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
    font-weight: 600;
    font-size: 14px;
    height: 60px;
    width: 110px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border: 1px solid rgba(0,0,0,0.06);
    border-radius: 0px;
    -webkit-box-shadow: 0 0 0 white, 0 0 0 white;
            box-shadow: 0 0 0 white, 0 0 0 white;
}

.button-text{
    display: inline-block;
    position: relative;
    z-index: 2;
    background: -webkit-gradient(linear, left top, right top, from(#504bff), to(#4cfa63));
    background: -webkit-linear-gradient(left, #504bff, #4cfa63);
    background: -o-linear-gradient(left, #504bff, #4cfa63);
    background: linear-gradient(to right, #504bff, #4cfa63);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.button-circle{
    position: absolute;
    left: 0;
    top: 0;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 10px #504bff, 0 0 20px #504bff, 0 0 30px #504bff;
            box-shadow: 0 0 10px #504bff, 0 0 20px #504bff, 0 0 30px #504bff;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.button-circle2{
    top: auto;
    bottom: 0;
}

.button-circle3{
    left: auto;
    right: 0;
    -webkit-box-shadow: 0 0 10px #4cfa63, 0 0 20px #4cfa63, 0 0 30px #4cfa63;
            box-shadow: 0 0 10px #4cfa63, 0 0 20px #4cfa63, 0 0 30px #4cfa63;
}

.button-circle4{
    left: auto;
    top: auto;
    bottom: 0;
    right: 0;
    -webkit-box-shadow: 0 0 10px #4cfa63, 0 0 20px #4cfa63, 0 0 30px #4cfa63;
            box-shadow: 0 0 10px #4cfa63, 0 0 20px #4cfa63, 0 0 30px #4cfa63;
}

.button:hover{
    -webkit-box-shadow: -15px 0 70px -15px #504bff, 15px 0 70px -15px #4cfa63;
            box-shadow: -15px 0 70px -15px #504bff, 15px 0 70px -15px #4cfa63;
}

.button:hover .button-circle1{
    -webkit-transform: translate(-15px, -15px) scale(0);
        -ms-transform: translate(-15px, -15px) scale(0);
            transform: translate(-15px, -15px) scale(0);
}

.button:hover .button-circle2{
    -webkit-transform: translate(-15px, 15px) scale(0);
        -ms-transform: translate(-15px, 15px) scale(0);
            transform: translate(-15px, 15px) scale(0);
}

.button:hover .button-circle3{
    -webkit-transform: translate(15px, -15px) scale(0);
        -ms-transform: translate(15px, -15px) scale(0);
            transform: translate(15px, -15px) scale(0);
}

.button:hover .button-circle4{
    -webkit-transform: translate(15px, 15px) scale(0);
        -ms-transform: translate(15px, 15px) scale(0);
            transform: translate(15px, 15px) scale(0);
}

.button:hover .button-circle{
    -webkit-box-shadow: none;
            box-shadow: none;
}

.popup{
    opacity: 0;
    visibility: hidden;
    height: 350px;
    width: 350px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    border-radius: 3px;
    position: relative;
    z-index: 3;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease
}

.popup-inside{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 0 black;
            box-shadow: 0 0 0 black;
    -webkit-transition:
            border-radius 0.35s ease 0.7s,
            -webkit-box-shadow 0.5s ease 0.7s;
    transition:
            border-radius 0.35s ease 0.7s,
            -webkit-box-shadow 0.5s ease 0.7s;
    -o-transition:
            box-shadow 0.5s ease 0.7s,
            border-radius 0.35s ease 0.7s;
    transition:
            box-shadow 0.5s ease 0.7s,
            border-radius 0.35s ease 0.7s;
    transition:
            box-shadow 0.5s ease 0.7s,
            border-radius 0.35s ease 0.7s,
            -webkit-box-shadow 0.5s ease 0.7s;
}

.backgrounds{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.background{
    --offset: 0;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: -webkit-gradient(linear, left top, right top, from(#504bff), to(#4cfa63));
    background: -webkit-linear-gradient(left, #504bff, #4cfa63);
    background: -o-linear-gradient(left, #504bff, #4cfa63);
    background: linear-gradient(to right, #504bff, #4cfa63);
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
    border-radius: 50%;
}

.background2{
    --offset: 10%;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: -webkit-gradient(linear, left top, right top, from(#6665ff), to(#69fa7f));
    background: -webkit-linear-gradient(left, #6665ff, #69fa7f);
    background: -o-linear-gradient(left, #6665ff, #69fa7f);
    background: linear-gradient(to right, #6665ff, #69fa7f);
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all 0.5s ease 0.1s;
    -o-transition: all 0.5s ease 0.1s;
    transition: all 0.5s ease 0.1s;
}

.background3{
    --offset: 20%;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: -webkit-gradient(linear, left top, right top, from(#8583ff), to(#85fa99));
    background: -webkit-linear-gradient(left, #8583ff, #85fa99);
    background: -o-linear-gradient(left, #8583ff, #85fa99);
    background: linear-gradient(to right, #8583ff, #85fa99);
    z-index: 2;
    -webkit-transition: all 0.5s ease 0.2s;
    -o-transition: all 0.5s ease 0.2s;
    transition: all 0.5s ease 0.2s;
}

.background4{
    --offset: 30%;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: -webkit-gradient(linear, left top, right top, from(#aaaaff), to(#a7fab7));
    background: -webkit-linear-gradient(left, #aaaaff, #a7fab7);
    background: -o-linear-gradient(left, #aaaaff, #a7fab7);
    background: linear-gradient(to right, #aaaaff, #a7fab7);
    z-index: 3;
    -webkit-transition: all 0.5s ease 0.3s;
    -o-transition: all 0.5s ease 0.3s;
    transition: all 0.5s ease 0.3s;
}

.background5{
    --offset: 40%;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: -webkit-gradient(linear, left top, right top, from(#c9c8ff), to(#c3fad1));
    background: -webkit-linear-gradient(left, #c9c8ff, #c3fad1);
    background: -o-linear-gradient(left, #c9c8ff, #c3fad1);
    background: linear-gradient(to right, #c9c8ff, #c3fad1);
    z-index: 4;
    -webkit-transition: all 0.5s ease 0.4s;
    -o-transition: all 0.5s ease 0.4s;
    transition: all 0.5s ease 0.4s;
}

.background6{
    --offset: 40%;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    background: white;
    z-index: 5;
    -webkit-transition: all 0.8s ease 0.4s;
    -o-transition: all 0.8s ease 0.4s;
    transition: all 0.8s ease 0.4s;
}

.content{
    --offset: 0;
    position: absolute;
    left: var(--offset);
    right: var(--offset);
    bottom: var(--offset);
    top: var(--offset);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    opacity: 0;
    -webkit-transition: all 0.35s ease 0.75s;
    -o-transition: all 0.35s ease 0.75s;
    transition: all 0.35s ease 0.75s;
    z-index: 10;
}

.content-wrapper{
    text-align: center;
}

.modal.active .content{
    opacity: 1;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
}

.modal.active .popup{
    opacity: 1;
    visibility: visible;
}

.modal.active .popup-inside{
    border-radius: 0;
    -webkit-box-shadow: -50px 0 200px -50px #504bff, 50px 0 200px -50px #4cfa63;
            box-shadow: -50px 0 200px -50px #504bff, 50px 0 200px -50px #4cfa63;
}

.modal.active .background{
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.modal.active .background6{
